import React, { FormEvent, useEffect, useState } from 'react';
import { Button, Modal, ModalHeader, ModalFooter, ModalBody, FormGroup, Label, Input,
    ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem} from 'reactstrap';
import { get, forEach, map } from 'lodash';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { toast } from 'react-toastify';
import Typeahead from '../Input/Typeahead';
import {createReport, updateReport} from "../../redux/BaoCaoLamViec/action";
import {getAllRoles, getAllRolesWithoutPage} from "../../redux/Roles/action";
import {registerUser} from "../../redux/User/action";

interface Props {
    onHide: () => void;
    onSuccessCreated: () => void;
    visible: boolean;
    modalTitle: string;
    item?: any;
}

const ModalTaoMoiUser: React.FC<Props> = (props: Props): JSX.Element => {
    const [listRoles, setListRoles] = useState<any>([]);
    const [idRole, setIdRole] = useState<number>(0);
    const [fullName, setFullName] = useState<string>('');
    const [nameRole, setNameRole] = useState<string>('');
    const [genderName, setGenderName] = useState<string>('');
    const [phoneNumber, setPhoneNumber] = useState<string>('');
    const [password, setPassword] = useState<string>('');
    const [gender, setGender] = useState<number>(1);
    const [email, setEmail] = useState<string>('');

    const { t } = useTranslation();
    const dispatch = useDispatch();
    const { onHide, visible, modalTitle, item, onSuccessCreated } = props;
    const [dropdownOpen, setOpen] = useState(false);
    const [dropdownOpenGender, setOpenGender] = useState(false);
    const toggle = () => setOpen(!dropdownOpen);
    const toggleGender = () => setOpenGender(!dropdownOpenGender);

    useEffect(() => {
        dispatch(
            getAllRolesWithoutPage(
                (data: any) => {
                    setListRoles(data.list)
                },
                () => {}
            )
        )
    },[visible])
    function handleCreate(e: FormEvent): void {
        e.preventDefault();
        if (!email || !phoneNumber || !fullName || !gender || !password || !get(listRoles, '0.id')) {
            toast(
                <>
                    <i className="fa fa-window-close mr-2" />
                    {t('không được để trống các trường')}
                </>,
                {
                    type: 'error',
                },
            );
            return
        }
        const payloadCreate = {
            fullName: fullName,
            idRole: idRole,
            phoneNumber: phoneNumber,
            email: email,
            gender: gender,
            password: password
        };

        if(!get(item, 'original.id')){
            dispatch(
                registerUser(payloadCreate,
                    (data: any):void => {
                        onSuccessCreated()
                        toast(
                            <>
                                <i className="fa fa-check-square mr-2" />
                                {t('Tạo người dùng ')} {t('thành công')}
                            </>,
                            {
                                type: 'success',
                            },
                        );
                    },
                    (): void => {
                        toast(
                            <>
                                <i className="fa fa-window-close mr-2" />
                                {t('Đã có lỗi xảy ra ')}
                            </>,
                            {
                                type: 'error',
                            },
                        );
                    }
                    ),
            );

        } else {
            // dispatch(
            //     updateReport({...payloadCreate, idReport: get(item, 'original.id')},
            //         (data: any): void => {
            //             onSuccessCreated();
            //             // const thisId = get(data, 'MT_ZTMI016_OUT.IV_TOR_ID_CU', '');
            //             toast(
            //                 <>
            //                     <i className="fa fa-check-square mr-2" />
            //                     {t('Cập nhật báo cáo')} {t('thành công')}
            //                 </>,
            //                 {
            //                     type: 'success',
            //                 },
            //             );
            //             // alert(`Tạo thành công! Mã: ${thisId}`);
            //         },
            //         (): void => {
            //             toast(
            //                 <>
            //                     <i className="fa fa-window-close mr-2" />
            //                     {t('Đã có lỗi xảy ra ')}
            //                 </>,
            //                 {
            //                     type: 'error',
            //                 },
            //             );
            //             // alert(t('Có lỗi xảy ra!'));
            //         },
            //     ),
            // );
        }
    }
    function handleChangeTextboxValue(setValueFunction: Function): (event: React.FormEvent<HTMLInputElement>) => void {
        return (event: React.FormEvent<HTMLInputElement>): void => {
            setValueFunction(event.currentTarget.value);
        };
    }
    function handleChangeGenderValue(v: number,genderMame: string):(event: React.MouseEvent) => void {
        return (event: React.MouseEvent): void => {
            setGender(v)
            setGenderName(genderMame)
        }
    }
    function handleChangeRoleValue(v: number,nameRole: string): (event: React.MouseEvent)  => void {
        return (event: React.MouseEvent): void => {
            console.log("role id = ",v)
            setIdRole(v)
            setNameRole(nameRole)

        }
    }
    return(
        <Modal isOpen={visible} className="sipTitleModalCreateNew">
            <ModalHeader toggle={onHide}>{modalTitle}</ModalHeader>
            <ModalBody>
                <FormGroup>
                    <Label>{t('Họ và tên')}</Label>
                    <Input
                        value={fullName}
                        onChange={handleChangeTextboxValue(setFullName)}
                        type="textarea"
                        placeholder={t('Nhập họ tên')}
                    />

                </FormGroup>
                <FormGroup>
                    <Label>{t('Email')}</Label>
                    <Input
                        value={email}
                        onChange={handleChangeTextboxValue(setEmail)}
                        type="textarea"
                        placeholder={t('Nhập Email')}
                    />
                </FormGroup>
                <FormGroup>
                    <Label>{t('Số điện thoại')}</Label>
                    <Input
                        value={phoneNumber}
                        onChange={handleChangeTextboxValue(setPhoneNumber)}
                        type="textarea"
                        placeholder={t('Nhập số điện thoại')}
                    />
                </FormGroup>
                <FormGroup>
                    <Label>{t('Mật khẩu')}</Label>
                    <Input
                        type={"password"}
                        value={password}
                        onChange={handleChangeTextboxValue(setPassword)}
                        placeholder={t('Nhập mật khẩu')}
                    />
                </FormGroup>
                <FormGroup>
                    <Label>{t('Chọn giới tính')}</Label>
                    <ButtonDropdown isOpen={dropdownOpenGender} toggle={toggleGender}>
                        <DropdownToggle caret>
                            {genderName}
                        </DropdownToggle>
                        <DropdownMenu>
                            <DropdownItem onClick={handleChangeGenderValue(1,"Nam")}>Nam</DropdownItem>
                            <DropdownItem onClick={handleChangeGenderValue(0,"Nữ")}>Nữ</DropdownItem>
                        </DropdownMenu>
                    </ButtonDropdown>
                </FormGroup>
                <FormGroup>
                    <Label>{t('Chọn quyền hạn')}</Label>
                    <ButtonDropdown isOpen={dropdownOpen} toggle={toggle}>
                        <DropdownToggle caret>
                            {nameRole}
                        </DropdownToggle>
                        <DropdownMenu>
                            {
                                map(listRoles, (item: any) => (
                                    <DropdownItem onClick={handleChangeRoleValue(item.id,item.roleName)}>{item.roleName}</DropdownItem>
                                ))
                            }
                        </DropdownMenu>
                    </ButtonDropdown>
                </FormGroup>
            </ModalBody>
            <ModalFooter className="justify-content-end">
                <Button color="primary" onClick={handleCreate}>
                    {t('Ghi lại')}
                </Button>
            </ModalFooter>
        </Modal>
    )
}
export default ModalTaoMoiUser;
